import LocationFreeTimeModal from '@/components/Modals/LocationFreeTimeModal';
import EditLocationModal from "./components/LocationModal";
import TreeModal from "./components/TreeModal";
import ToolBar from "./components/ToolBar";
var columns = [{
  title: '地点名称',
  scopedSlots: {
    customRender: 'name'
  }
}, {
  title: '开放时间',
  scopedSlots: {
    customRender: 'openTime'
  }
}, {
  slots: {
    title: 'positionTitle'
  },
  scopedSlots: {
    customRender: 'position'
  }
}, {
  title: '操作',
  scopedSlots: {
    customRender: 'operation'
  }
}];
export default {
  components: {
    LocationFreeTimeModal: LocationFreeTimeModal,
    EditLocationModal: EditLocationModal,
    ToolBar: ToolBar,
    TreeModal: TreeModal
  },
  data: function data() {
    return {
      locationData: [],
      currentLocation: {},
      locationFreeTimeModalVisible: false,
      editLocationModalVisible: false,
      columns: columns,
      loading: false,
      treeModalVisible: false
    };
  },
  computed: {
    isTop: function isTop(row) {
      return function (row) {
        return !row.parent;
      };
    }
  },
  created: function created() {
    this.init();
  },
  methods: {
    init: function init() {
      this.getLocationList();
    },
    getLocationList: function getLocationList() {
      var _this = this;

      this.loading = true;
      this.$store.dispatch('activity/getActivityLocationList').then(function (res) {
        _this.locationData = res;
        _this.loading = false;
      });
    },
    handleFreeTimeClick: function handleFreeTimeClick(data) {
      this.currentLocation = data;
      this.locationFreeTimeModalVisible = true;
    },
    handleEditLocationClick: function handleEditLocationClick(data) {
      this.currentLocation = data;
      this.editLocationModalVisible = true;
    },
    handleEditSuccess: function handleEditSuccess() {
      this.editLocationModalVisible = false;
      this.init();
    }
  }
};